.soup\.exe {
// pos
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
// size
    height: min(var(--m) * 8px, var(--m) * 9vw/16);
    --m: 60;
    aspect-ratio: 2.4;
}

.nav {
// size
    width: 0px;
}

.navC {
//size
    height: 100%;
    width: 100%;
    top: -1px;
}

/* ====overflow ctrl==== */

.banner, .main, .nav, .bottom {overflow: hidden;}

.navC, .content {overflow: hidden scroll}



/* ==== L A Y O U T T A B L E ==== */
//
//
.soup\.exe {
    display: flex;
}

/* ====soup.exe==== */

    .c1 {
        flex: 5;
        display: flex;
        flex-direction: column;
    }

    .c2 {
        flex: 2;
    }

    /* ====c1==== */

        .banner {
            flex: 2;
        }

        .main {
            flex: 7;
            display: flex;
        }

        /* ====main==== */

            .nav {
                flex: 3;
            }

            .content {
                flex: 10;
            }

        /* ======== */

        .bottom {
            flex: 1;
        }

